.main .content-body{padding-top:6em;max-width:64em;flex:1 1;margin:auto}.main .content-body .error{color:#FF4800}.main .content-body .filters{height:4em;display:flex;width:100%;justify-content:space-between}.main .content-body .filters .location,.main .content-body .filters .unit{background-color:#fff;border-radius:10px;box-shadow:0px 1px 7px rgba(0,0,0,0.15);display:flex;align-items:center}.main .content-body .filters .location{padding:0 1em}.main .content-body .filters .location .city-select{width:8em}.main .content-body .filters .location .city-select #grouped-select,.main .content-body .filters .location .city-select label{color:#636B78}.main .content-body .filters .unit{padding:0 0 0 1em}.main .content-body .filters .unit .custom-fieldset{justify-content:center}.main .content-body .filters .unit .custom-fieldset .custom-radio{flex-direction:row}.main .content-body .filters .unit .custom-fieldset .custom-radio label span,.main .content-body .filters .unit .custom-fieldset .custom-radio label+span{color:#636B78}.main .content-body .filters .unit .custom-fieldset .custom-radio label .Mui-checked span,.main .content-body .filters .unit .custom-fieldset .custom-radio label .Mui-checked+span{color:#FF4800}.main .content-body .card-parent{margin-top:1em;width:100%;margin:0 0 4em}.main .content-body .card-parent .cards{display:flex;justify-content:space-between;width:100%;overflow-x:visible;padding:1em 0}.main .content-body .card-parent .cards.underThree{justify-content:flex-start}.main .content-body .card-parent .cards.underThree>.card{margin-right:5em}.main .content-body .card-parent .card-control{display:flex;width:100%;justify-content:space-between;margin:0.5em 0}.main .content-body .card-parent .card-control button{border:0;background:transparent;display:flex;align-items:center;color:#399BDA}.main .content-body .card-parent .card-control button:hover{cursor:pointer}.main .content-body .chart{display:flex;justify-content:center}.main .content-body .chart .chart-pla