.card{align-items:center;box-shadow:0px 1px 7px rgba(0,0,0,0.15);border-radius:0px 10px 10px 0px;display:flex;height:14.5em;padding-left:2em;position:relative;width:32%;background-color:#fff}.card.active{box-shadow:0px 1px 7px rgba(255,72,0,0.4);border:1px solid rgba(255,72,0,0.5)}.card:hover{cursor:pointer;box-shadow:0px 1px 7px rgba(255,72,0,0.4)}.card .outline{background-color:#FF4800;width:0.4em;height:100%;position:absolute;left:0;top:0}.card .details{flex:1 1}.card .details .region{font-size:34px;margin:0.3em 0}.card .details .region,.card .details .temp{color:#636B78}.card .details .temp{font-size:54px;margin:0.1em 0}.card .details .temp .degrees{font-size:14px;position:relative;top:-1em;font-weight:bold}.card .details .temp .unit{font-size:24px;position:relative;top:-0.2em}.card .details .date{color:#399BDA;font-size:16px}.card .details .bottom{display:flex;width:100%}.card .details .bottom .bottom-item{display:flex;flex-direction:column;flex:1 1}.card .details .bottom .bottom-item span:first-child{color:#399BDA;font-size:12px;text-transform:capitalize}.card .details .bottom .bottom-item span:last-child{color:#636B78;font-size:16px;margin:0.3em 0}.card .additional{flex:1 1;align-self:flex-start;margin-top:2em;align-items:center;display:flex;flex-direction:column}.card .additional p{font-size:12px;color:#636B78;text-transform:capitalize}.card .additional img{width:7em;height:auto}@media only screen and (max-width: 920px){.card{flex-direction:column;height:auto;padding:0}.card .details,.card .additional{width:100%;display:flex;flex-direction:column;align-items:center;justify-content:center}.card .details .bottom,.card .additional .bottom{text-align:center}}@media only screen and (max-width: 780px){.card .additional{display:none}}@media only screen and (max-width: 579px){.card{width:100%;margin-bottom:1em}}

